.parent {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 10px; /* Adjust spacing between videos */
    padding: 20px; /* Adjust container padding */
    width: 100%;
    height: 100%;
}

.video-container {
    border: 1px solid black;
    padding: 10px;
    width: 100%;
    height: 100%;
}